//配置路由
import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Mdetail from '@/pages/detail'
import Home from '@/pages/home'
import Mhomecontent from '@/pages/homecontent'

export default new VueRouter({
	routes: [
		{
			path: '/',
			component: Home,
		},
		{
			path: '/detail',
			component: Mdetail,
		},
		{
			path: '/homecontent',
			component: Mhomecontent,
		},
	],
})
